import { Button, Typography } from "@mui/material";
import React from "react";

const styles = {
    root: {
        display: 'flex',
        gap: '15px',
        alignItems: 'center'
    }
}

export const Counter = ({ Number, setNumber }) => {
    return (
        <div style={styles.root}>
            <Button
                onClick={() => Number && setNumber(Number - 1)}
                children={<Typography variant='h5' children={'-'} />}
            />

            <Typography variant='h6' children={Number} />

            <Button
                onClick={() => setNumber(Number + 1)}
                children={<Typography variant='h5' children={'+'} />}
            />
        </div>
    )

};
